import { Component } from '@angular/core';
import { UserService } from '../shared/user.service';
import { HttpErrorResponse } from '@angular/common/http';
import { NavController, NavParams } from '@ionic/angular';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: 'login.page.html',
  styleUrls: ['login.page.scss']
})

export class LoginPage {
  isLoginError : boolean = false;
  isLogging : boolean = false;
  loginCredentials = { UserName: '', Password: '' };

  constructor(private userService : UserService, private router : Router, public navCtrl: NavController) { }

  onSubmit(){
    this.isLoginError = false;
    this.isLogging = true;    
    this.userService.userAuthentication(this.loginCredentials.UserName, this.loginCredentials.Password).subscribe((data:any) =>{
      localStorage.setItem('userToken',data.access_token);
      localStorage.setItem('userRoles',data.role);
      localStorage.setItem('userName',data.username);
      this.isLogging = false;
      this.router.navigate(['/home']);
    },
    (err : HttpErrorResponse) => {
      this.isLogging = false;
      this.isLoginError = true;
    });
  }

}

